var scrapeADP = function scrapeADP(html) {
  var players = {
    RB: [],
    QB: [],
    WR: [],
    DEF: [],
    PK: [],
    TE: []
  };
  var $ = cheerio.load(html);
  $('tr').each(function (i, elem) {
    var position = $(elem).find('td').eq(3).text();

    if (position == 'RB') {
      players.RB.push({
        name: $(elem).find('td').eq(2).text(),
        pos: position,
        pick: $(elem).find('td').eq(0).text(),
        team: $(elem).find('td').eq(4).text(),
        bye: $(elem).find('td').eq(5).text()
      });
    } else if (position == 'WR') {
      players.WR.push({
        name: $(elem).find('td').eq(2).text(),
        pos: position,
        pick: $(elem).find('td').eq(0).text(),
        team: $(elem).find('td').eq(4).text(),
        bye: $(elem).find('td').eq(5).text()
      });
    } else if (position == 'QB') {
      var name = null;

      if ($(elem).find('td').eq(2).text() == 'Pat Mahomes') {
        name = 'Patrick Mahomes';
      } else {
        name = $(elem).find('td').eq(2).text();
      }

      players.QB.push({
        name: name,
        pos: position,
        pick: $(elem).find('td').eq(0).text(),
        team: $(elem).find('td').eq(4).text(),
        bye: $(elem).find('td').eq(5).text()
      });
    } else if (position == 'TE') {
      players.TE.push({
        name: $(elem).find('td').eq(2).text(),
        pos: position,
        pick: $(elem).find('td').eq(0).text(),
        team: $(elem).find('td').eq(4).text(),
        bye: $(elem).find('td').eq(5).text()
      });
    } else if (position == 'PK') {
      players.PK.push({
        name: $(elem).find('td').eq(2).text(),
        pos: position,
        pick: $(elem).find('td').eq(0).text(),
        team: $(elem).find('td').eq(4).text(),
        bye: $(elem).find('td').eq(5).text()
      });
    } else if (position == 'DEF') {
      players.DEF.push({
        name: $(elem).find('td').eq(2).text(),
        pos: position,
        pick: $(elem).find('td').eq(0).text(),
        team: $(elem).find('td').eq(4).text(),
        bye: $(elem).find('td').eq(5).text()
      });
    } else {
      return null;
    }
  });
  return players;
};

var scrapePlayerStats = function scrapePlayerStats(player, html) {
  var COMMENT_PSEUDO_COMMENT_OR_LT_BANG = new RegExp('<!--[\\s\\S]*?(?:-->)?' + '<!---+>?' // A comment with no body
  + '|<!(?![dD][oO][cC][tT][yY][pP][eE]|\\[CDATA\\[)[^>]*>?' + '|<[?][^>]*>?', // A pseudo-comment
  'g');
  var data = html.replace(COMMENT_PSEUDO_COMMENT_OR_LT_BANG, "");
  var $ = cheerio.load(data);
  var picture = $('[itemscope=image]').attr('src');

  if (player.pos === 'DEF') {
    var row = $('#defense tfoot tr td');
    var defStats = {
      name: player.name,
      picture: picture,
      interceptions: row.eq(5).text(),
      pick_six: row.eq(7).text(),
      sacks: row.eq(15).text(),
      forced_fumbles: row.eq(10).text(),
      safties: row.eq(21).text()
    };
    return defStats;
  }

  var playerStats = {
    name: player.name,
    picture: picture,
    ir: {},
    career: []
  };
  var ir = $('#injury');

  if (ir) {
    var info = ir.find('p').text();

    if (info.length > 0) {
      playerStats.ir = {
        injury: info
      };
    } else {
      playerStats.ir = {
        injury: null
      };
    }
  }

  if (player.pos === 'RB' || player.pos === 'QB') {
    var years = $('#rushing_and_receiving tr.full_table');
    years.each(function (i, obj) {
      var row = $(obj).find('td');
      var year = years.find('th').eq(i).text().substring(0, 4);
      playerStats.career.push({
        age: row.eq(0).text(),
        team: row.eq(1).find('a').attr('title'),
        season: year,
        position: player.pos,
        number: row.eq(3).text(),
        games: row.eq(4).text(),
        games_started: row.eq(5).text(),
        skills: [{
          attempts: row.eq(6).text(),
          yards: row.eq(7).text(),
          touchdowns: row.eq(8).text(),
          longest_attempt: row.eq(9).text(),
          yards_per_attempt: row.eq(10).text(),
          yards_per_game: row.eq(11).text(),
          attempts_per_game: row.eq(12).text()
        }, {
          targets: row.eq(13).text(),
          receptions: row.eq(14).text(),
          yards: row.eq(15).text(),
          yard_per_reception: row.eq(16).text(),
          touchdowns: row.eq(17).text(),
          longest_reception: row.eq(18).text(),
          receptions_per_game: row.eq(19).text(),
          yards_per_game: row.eq(20).text(),
          catch_percentage: row.eq(21).text(),
          yards_per_target: row.eq(22).text()
        }],
        summary: {
          touches: row.eq(23).text(),
          yards_per_touch: row.eq(24).text(),
          scrimmage: row.eq(25).text(),
          total_touchdowns: row.eq(26).text()
        },
        fumbles: row.eq(27).text()
      });
    });

    if (player.pos == 'QB') {
      var rows = $('#passing tr.full_table');
      rows.each(function (i, obj) {
        var row = $(obj).find('td');
        var year = years.find('th').eq(i).text().substring(0, 4);
        playerStats.career[i].skills.push({
          qb_record: row.eq(6).text(),
          completions: row.eq(7).text(),
          yards: row.eq(8).text(),
          touchdowns: row.eq(9).text(),
          touchdown_percentage: row.eq(10).text(),
          interceptions: row.eq(11).text(),
          interception_percentage: row.eq(12).text(),
          longest_completion: row.eq(13).text(),
          yards_per_attempt: row.eq(14).text(),
          adjusted_yards_per_attempt: row.eq(15).text(),
          yards_per_completion: row.eq(16).text(),
          yards_per_game: row.eq(17).text(),
          qbr: row.eq(18).text()
        });
      });
    }
  } else if (player.pos === 'WR' || player.pos === 'TE') {
    var _years = $('#receiving_and_rushing tr.full_table');

    _years.each(function (i, obj) {
      var row = $(obj).find('td');

      var year = _years.find('th').eq(i).text().substring(0, 4);

      playerStats.career.push({
        age: row.eq(0).text(),
        team: row.eq(1).find('a').attr('title'),
        season: year,
        position: player.pos,
        number: row.eq(3).text(),
        games: row.eq(4).text(),
        games_started: row.eq(5).text(),
        skills: [{
          targets: row.eq(6).text(),
          receptions: row.eq(7).text(),
          yards: row.eq(8).text(),
          yard_per_reception: row.eq(9).text(),
          touchdowns: row.eq(10).text(),
          longest_reception: row.eq(11).text(),
          receptions_per_game: row.eq(12).text(),
          yards_per_game: row.eq(13).text(),
          catch_percentage: row.eq(14).text(),
          yards_per_target: row.eq(15).text()
        }, {
          attempts: row.eq(16).text(),
          yards: row.eq(17).text(),
          touchdowns: row.eq(18).text(),
          longest_attempt: row.eq(19).text(),
          yards_per_attempt: row.eq(20).text(),
          yards_per_game: row.eq(21).text(),
          attempts_per_game: row.eq(22).text()
        }],
        summary: {
          touches: row.eq(23).text(),
          yards_per_touch: row.eq(24).text(),
          scrimmage: row.eq(25).text(),
          total_touchdowns: row.eq(26).text()
        },
        fumbles: row.eq(27).text()
      });
    });
  } else if (player.pos === 'PK') {
    var _years2 = $('#kicking tbody tr');

    _years2.each(function (i, obj) {
      var row = $(obj).find('td');

      var year = _years2.find('th').eq(i).text().substring(0, 4);

      playerStats.career.push({
        age: row.eq(0).text(),
        team: row.eq(1).find('a').attr('title'),
        season: year,
        position: player.pos,
        number: row.eq(3).text(),
        games: row.eq(4).text(),
        '0-19': {
          attempts: row.eq(6).text(),
          made: row.eq(7).text()
        },
        '20-29': {
          attempts: row.eq(8).text(),
          made: row.eq(9).text()
        },
        '30-39': {
          attempts: row.eq(10).text(),
          made: row.eq(11).text()
        },
        '40-49': {
          attempts: row.eq(12).text(),
          made: row.eq(13).text()
        },
        '50+': {
          attempts: row.eq(14).text(),
          made: row.eq(15).text()
        },
        total: {
          attempts: row.eq(16).text(),
          made: row.eq(17).text()
        },
        extra_points: {
          attempts: row.eq(20).text(),
          made: row.eq(21).text()
        }
      });
    });
  }

  return playerStats;
};

var getADP = function getADP() {
  return new Promise(function (resolve, reject) {
    axios.get(url, {
      headers: {
        'origin': 1
      }
    }).then(function (resp) {
      var players = scrapeADP(resp.data);

      if (players) {
        resolve(players);
      } else {
        reject('Found player that was not in a known position!');
      }
    }).catch(function (err) {
      reject(err);
    });
  });
};

var getLastInitial = function getLastInitial(name) {
  var nameArray = name.trim().toUpperCase().replace('JR.', '').replace(' JR', '').replace('JR ').split(' ');
  var initial = nameArray[nameArray.length - 1].charAt(0);
  return initial;
};

var getPlayerStats = function getPlayerStats(player) {
  var pfr = 'https://cors-anywhere.herokuapp.com/https://www.pro-football-reference.com';
  var team = pfrDict[player.team];
  var teamPage = "".concat(pfr, "/teams/").concat(team);
  var initial = getLastInitial(player.name);

  if (player.pos === 'DEF') {
    return new Promise(function (resolve, reject) {
      axios.get(teamPage, {
        headers: {
          'origin': 1
        }
      }).then(function (res) {
        var $ = cheerio.load(res.data);
        var year = $('[data-stat=year_id] a').eq(0).attr('href');
        var teamYearSum = "".concat(pfr).concat(year);
        axios.get(teamYearSum, {
          headers: {
            'origin': 1
          }
        }).then(function (res) {
          resolve(scrapePlayerStats(player, res.data));
        }).catch(function (err) {
          console.log(err);
        });
      }).catch(function (err) {
        console.log(err);
      });
    });
  }

  return new Promise(function (resolve, reject) {
    var playersPageURL = "".concat(pfr, "/players/").concat(initial);
    axios.get(playersPageURL, {
      headers: {
        'origin': 1
      }
    }).then(function (res) {
      var playersPage = cheerio.load(res.data);
      playersPage('#div_players p b').each(function (i, obj) {
        var a = playersPage(obj).find('a').eq(0);
        var pfrTag = playersPage(obj).html();
        var pfrName = a.text().toLowerCase().replace('jr.', '').replace(' jr', '').replace('jr ', '').replace("'", "");
        var adpName = player.name.toLowerCase().replace('jr.', '').replace(' jr', '').replace('jr ', '').replace("'", "");

        if (pfrName === adpName && pfrTag.search(player.pos) != -1) {
          var statsPageRef = a.attr('href');
          var statsPageURL = "".concat(pfr).concat(statsPageRef);
          axios.get(statsPageURL, {
            headers: {
              'origin': 1
            }
          }).then(function (res) {
            resolve(scrapePlayerStats(player, res.data));
          }).catch(function (err) {
            reject(err);
          });
        }
      }, function () {
        reject('Didnt find anything with this URL!');
      });
    }).catch(function (err) {
      reject(err);
    });
  });
};

module.exports = {
  getADP: getADP,
  getPlayerStats: getPlayerStats
};